<template>
    <form @submit="onSubmit" class="add-form">
        <div class="form-control">
            <label>Task</label>
            <input type="text" v-model="text" name="text" placeholder="Add Task" />
        </div>
        <div class="form-control">
            <label>Day & Time</label>
            <input type="text" id="day" name="dateTimePicker" placeholder="Add Day & Time" readonly />
        </div>
        <input type="submit" value="Save Task" class="btn btn-block" />
    </form>
</template>

<script>
export default {
    name: 'AddTask',
    data() {
        return {
            text: '',
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault()
            const day = document.getElementById('day').value;

            if (!this.text) {
                myFunction("Please add a task")
                return
            }

            if (!day) {
                myFunction("Please add date and time")
                return
            }

            if (new Date(day) < new Date()) {
                myFunction("Please select future time")
                return
            }

            const newTask = {
                id: this.$parent.getId(),
                text: this.text,
                day: day,
            }

            this.$emit('add-task', newTask)

            this.text = ''
            document.getElementById('day').value = ''
        }
    },
}
</script>

<style scoped>
.add-form {
    margin-bottom: 40px;
}

.form-control {
    margin: 20px 0;
}

.form-control label {
    display: block;
}

.form-control input {
    width: 100%;
    height: 40px;
    margin:  5px 0;
    padding:  3px 7px;
    font-size: 17px;
}

.form-control-check {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: left;
}

.form-control-check label {
    margin-right: 1em;
}

.form-control-check input {
    height: 20px;
    width: 20px;
}
</style>
